<template>
  <div class="date-wrapper">
    <!-- 年份 月份 -->
    <div class="month">
      <span class="btn" @click="weekPre"
        ><Icon type="arrow-left" :size="12" /> 上一周</span
      >
      <p class="date" v-if="false">{{ currentYear }}年{{ currentMonth }}月</p>
      <span class="btn-today" @click="today">今天</span>
      <span class="btn" @click="weekNext"
        >下一周 <Icon type="arrow-right" :size="12"
      /></span>
    </div>
    <!-- 星期 -->
    <ul class="weekdays">
      <li>日</li>
      <li>一</li>
      <li>二</li>
      <li>三</li>
      <li>四</li>
      <li>五</li>
      <li>六</li>
    </ul>
    <!-- 日期 -->
    <ul class="days">
      <li
        :class="['day', currentClickDay === formatDate(day.getFullYear(), day.getMonth() + 1, day.getDate()) ? 'current' : '']"
        @click="pick(day)"
        v-for="(day, index) in days"
        :key="index"
      >
        <div class="week-no">{{ weekArr[index] }}</div>
        <!--本月-->
        <span v-if="day.getMonth() + 1 != currentMonth" class="other-month">
          <span
            v-if="
              day.getFullYear() == new Date().getFullYear() &&
              day.getMonth() == new Date().getMonth() &&
              day.getDate() == new Date().getDate()
            "
            class="active"
            >{{ day.getDate() }}</span
          >
          <span v-else>{{ day.getDate() }}</span>
        </span>
        <div v-else class="day-no">
          <!--今天-->
          <span
            v-if="
              day.getFullYear() == new Date().getFullYear() &&
              day.getMonth() == new Date().getMonth() &&
              day.getDate() == new Date().getDate()
            "
            class="active"
            >{{ day.getDate() }}</span
          >
          <span v-else>{{ day.getDate() }}</span>
        </div>
        <ul
          class="symbol-box"
          v-if="daysData[formatDate(day.getFullYear(), day.getMonth() + 1, day.getDate())]"
        >
          <li
            v-if="daysData[formatDate(day.getFullYear(), day.getMonth() + 1, day.getDate())].lunch"
            class="symbol lunch"
          >
            L
          </li>
          <li
            v-if="daysData[formatDate(day.getFullYear(), day.getMonth() + 1, day.getDate())].dinner"
            class="symbol dinner"
          >
            D
          </li>
        </ul>
      </li>
    </ul>
  </div>
</template>
<script>
import Icon from '@/components/Icon'
// eslint-disable-next-line no-extend-native
Date.prototype.format = function (fmt) {
  const o = {
    'M+': this.getMonth() + 1, // 月份
    'd+': this.getDate(), // 日
    'h+': this.getHours(), // 小时
    'm+': this.getMinutes(), // 分
    's+': this.getSeconds(), // 秒
    'q+': Math.floor((this.getMonth() + 3) / 3), // 季度
    S: this.getMilliseconds() // 毫秒
  }
  if (/(y+)/.test(fmt)) {
    fmt = fmt.replace(
      RegExp.$1,
      (this.getFullYear() + '').substr(4 - RegExp.$1.length)
    )
  }
  for (let k in o) {
    if (new RegExp('(' + k + ')').test(fmt)) {
      fmt = fmt.replace(
        RegExp.$1,
        RegExp.$1.length === 1 ? o[k] : ('00' + o[k]).substr(('' + o[k]).length)
      )
    }
  }
  return fmt
}
export default {
  name: 'date',
  props: {
    daysData: Object
  },
  components: {
    Icon
  },
  data () {
    return {
      currentYear: 1970, // 年份
      currentMonth: 1, // 月份
      currentDay: 1, // 日期
      currentWeek: 1, // 星期
      days: [],
      weekArr: ['周一', '周二', '周三', '周四', '周五', '周六', '周日'],
      currentClickDay: ''
    }
  },

  mounted () {
    this.$emit('updateDate', `${this.currentYear}年${this.currentMonth}月`, this.days)
  },

  created () {
    this.initData(null)
  },

  methods: {
    formatDate (year, month, day) {
      const y = year
      let m = month
      if (m < 10) m = `0${m}`
      let d = day
      if (d < 10) d = `0${d}`
      return `${y}-${m}-${d}`
    },

    initData (cur) {
      let date = ''
      if (cur) {
        date = new Date(cur)
      } else {
        date = new Date()
      }
      this.currentDay = date.getDate() // 今日日期 几号
      this.currentYear = date.getFullYear() // 当前年份
      this.currentMonth = date.getMonth() + 1 // 当前月份
      this.currentWeek = date.getDay() // 1...6,0   // 星期几
      if (this.currentWeek === 0) {
        this.currentWeek = 7
      }
      const str = this.formatDate(
        this.currentYear,
        this.currentMonth,
        this.currentDay
      ) // 今日日期 年-月-日
      this.days.length = 0
      // 今天是周日，放在第一行第7个位置，前面6个 这里默认显示一周，如果需要显示一个月，则第二个循环为 i<= 35- this.currentWeek
      /* eslint-disabled */
      for (let i = this.currentWeek - 1; i >= 0; i -= 1) {
        const d = new Date(str)
        d.setDate(d.getDate() - i)
        // console.log(y:" + d.getDate())
        this.days.push(d)
      }
      for (let i = 1; i <= 7 - this.currentWeek; i += 1) {
        const d = new Date(str)
        d.setDate(d.getDate() + i)
        this.days.push(d)
      }
    },

    //  上个星期
    weekPre () {
      const d = this.days[0] // 如果当期日期是7号或者小于7号
      d.setDate(d.getDate() - 7)
      this.initData(d)
      this.$emit('updateDate', `${this.currentYear}年${this.currentMonth}月`, this.days)
    },

    //  下个星期
    weekNext () {
      const d = this.days[6] // 如果当期日期是7号或者小于7号
      d.setDate(d.getDate() + 7)
      this.initData(d)
      this.$emit('updateDate', `${this.currentYear}年${this.currentMonth}月`, this.days)
    },

    // 上一個月   传入当前年份和月份
    pickPre (year, month) {
      const d = new Date(this.formatDate(year, month, 1))
      d.setDate(0)
      const newDate = this.formatDate(d.getFullYear(), d.getMonth() + 1, 1)
      this.initData(newDate)
    },

    // 下一個月   传入当前年份和月份
    pickNext (year, month) {
      const d = new Date(this.formatDate(year, month, 1))
      d.setDate(35)
      const newDate = this.formatDate(d.getFullYear(), d.getMonth() + 1, 1)
      this.initData(newDate)
    },
    today () {
      const today = new Date()
      this.initData(today.format('yyyy-MM-dd'))
      this.$emit('updateDate', `${this.currentYear}年${this.currentMonth}月`, this.days)
      this.$emit('pickDay', today.format('yyyy-MM-dd'))
      this.currentClickDay = ''
    },
    // 当前选择日期
    pick (date) {
      /*
      alert(
        this.formatDate(date.getFullYear(), date.getMonth() + 1, date.getDate())
      );
      */
      this.currentClickDay = date.format('yyyy-MM-dd')
      this.$emit('pickDay', date.format('yyyy-MM-dd'))
    }
  }
}
</script>
